.decentralized {
  width: 860px;
  margin: auto;
  margin-top: 50px;
  @media (max-width: 768px) {
    width: 100%;
    padding: 0 15px;
  }
  > :first-child {
    font-size: 20px;
    font-weight: 700;
  }
  > div {
    margin-bottom: 30px;
  }
  > div ~ div {
    color: rgb(111, 111, 111);
  }
}

.decentralized_icons {
  width: 1300px;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  @media (max-width: 768px) {
    width: 100%;
    display: block;
  }
  > div {
    width: 25%;
    text-align: center;
    @media (max-width: 768px) {
      width: 100%;
      text-align: center;
    }
    > div {
      color: black;
      font-weight: bold;
      font-size: 18px;
      margin-top: 70px;
      margin-bottom: 35px;
      @media (max-width: 768px) {
        // margin: auto;
      }
    }
    img {
      margin-bottom: 120px;
      @media (max-width: 768px) {
        margin: auto;
      }
    }
  }
}

.decentralized_half {
  display: flex;
  @media (max-width: 768px) {
    margin-top: 80px;
    flex-direction: column-reverse;
  }
  > div {
    width: 50%;
    min-height: 800px;
    @media (max-width: 768px) {
      width: 100%;
      height: 417px !important;
      min-height: unset;
    }
    &:first-child {
      background-color: rgba(30, 30, 30, 0.03);
      padding-top: 280px;
      @media (max-width: 768px) {
        padding: 0;
      }
      > div {
        width: 70%;
        margin: auto;
        @media (max-width: 768px) {
          width: 100%;
          padding: 0 15px;
          margin-top: 30px;
        }
      }
      .title {
        font-size: 22px;
        font-weight: bolder;
        margin-bottom: 30px;
        // @media (max-width: 768px) {
        //   font-size: 18px !important;
        // }
      }
    }
    &:nth-child(2) {
      background: url(../../assets/imgs/imgs/decentralized_bg1.png) center /
        cover no-repeat;
      > img {
        margin-top: 120px;
        margin-left: 50%;
        transform: translateX(-50%);
        @media (max-width: 768px) {
          width: 180px;
        }
      }
    }
  }
}
